body {
  font-size: 16px;
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu", "Helvetica Neue", sans-serif;
  color: #ffffff;
  background-color: #2B2B2B;
}
.accordion .frame .heading {
  background-color: #4D5254;
  color: #BBBBBB;
}
.accordion .frame {
  margin: 0;
}
.accordion .frame + .frame {
  border-top: 1px #373737 solid;
}
.app-bar {
  background-color: #4D5254;
  color: #BBBBBB;
}
.app-bar .dropdown-toggle::before {
  border-color: #BBBBBB;
}
.app-bar .app-bar-menu .d-menu,
.app-bar .app-bar-container .d-menu {
  background-color: #4D5254;
  color: #BBBBBB;
}
.app-bar .app-bar-menu .divider,
.app-bar .app-bar-container .divider {
  background-color: #6e6e6e;
}
@media screen and (min-width: 0) {
  .app-bar.app-bar-expand-fs .app-bar-menu .d-menu,
  .app-bar.app-bar-expand-fs .app-bar-container .d-menu {
    box-shadow: none;
    border: 1px solid #515151;
  }
}
@media screen and (min-width: 576px) {
  .app-bar.app-bar-expand-sm .app-bar-menu .d-menu,
  .app-bar.app-bar-expand-sm .app-bar-container .d-menu {
    box-shadow: none;
    border: 1px solid #515151;
  }
}
@media screen and (min-width: 768px) {
  .app-bar.app-bar-expand-md .app-bar-menu .d-menu,
  .app-bar.app-bar-expand-md .app-bar-container .d-menu {
    box-shadow: none;
    border: 1px solid #515151;
  }
}
@media screen and (min-width: 992px) {
  .app-bar.app-bar-expand-lg .app-bar-menu .d-menu,
  .app-bar.app-bar-expand-lg .app-bar-container .d-menu {
    box-shadow: none;
    border: 1px solid #515151;
  }
}
@media screen and (min-width: 1200px) {
  .app-bar.app-bar-expand-xl .app-bar-menu .d-menu,
  .app-bar.app-bar-expand-xl .app-bar-container .d-menu {
    box-shadow: none;
    border: 1px solid #515151;
  }
}
@media screen and (min-width: 1452px) {
  .app-bar.app-bar-expand-xxl .app-bar-menu .d-menu,
  .app-bar.app-bar-expand-xxl .app-bar-container .d-menu {
    box-shadow: none;
    border: 1px solid #515151;
  }
}
.bd-default {
  border-color: #515151 !important;
}
.button {
  background-color: #4D5254;
  color: #BBBBBB;
}
.button:active,
.button.focus,
.button:focus {
  box-shadow: 0 0 0 3px rgba(77, 82, 84, 0.45);
}
.calendar {
  border-color: #515151;
}
.calendar .calendar-header {
  background-color: #4D5254;
  color: #BBBBBB;
}
.calendar .day.today {
  background-color: rgba(77, 82, 84, 0.5);
}
.calendar .day.selected {
  box-shadow: 0 0 0 1px #4d5254 !important;
}
.calendar .day.selected::after {
  border-top-color: #4D5254;
}
.calendar .calendar-years > .years-list li.active,
.calendar .calendar-months > .months-list li.active {
  background-color: #4D5254;
  color: #BBBBBB;
}
